import Vue from 'vue'
import Router from 'vue-router'
import sy from './com/sy.vue'
import lie from './com/lie.vue'
import wo from './com/wo.vue'
import fa from './com/fa.vue'

import tui from './lie/tui.vue'
import bang from './lie/bang.vue'
import shu from './lie/shu.vue'
import te from './lie/te.vue'
import man from './lie/man.vue'

import cz from './lie/cz.vue'
import yue from './lie/yue.vue'

Vue.use(Router)

var routes = [
    {
        path: '/',
        name: 'sy',
        component:sy
    },
    {
        path: '/lie',
        name: 'lie',
        component:lie,
        children:[
            {
                path: '/',
                name: 'tui',
                component:tui
            },
            {
                path: 'bang',
                name: 'bang',
                component:bang
            },
            {
                path: 'shu',
                name: 'shu',
                component:shu
            },
            {
                path: 'te',
                name: 'te',
                component:te
            },
            {
                path: 'man',
                name: 'man',
                component:man
            }
        ]
    },
    {
        path: '/wo',
        name: 'wo',
        component:wo,
        children:[
            {
                path: 'yue',
                name: 'yue',
                component:yue
            },
            {
                path: 'cz',
                name: 'cz',
                component:cz
            }
        
        ]
    },
    {
        path: '/fa',
        name: 'fa',
        component:fa
    }
    ]
    
    export default new Router({
        routes
    })


