<template>
  <div>
    <div style="text-align:center;font-size:0.24rem;background:#ff5d59;padding:1.5rem 0;">
      <img
        style="width:2rem;height:2rem;border-radius: 50%;"
        src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=2940929,3706830473&fm=26&gp=0.jpg"
        alt
      />
      <p></p>
      <p>
        
        <span>ID</span>
      </p>
    </div>
    <a href="#/wo/cz" @click="bee()" style="color:#666;">
      <h3
        style="font-size:0.24rem;line-height:1rem;border-top:solid 0.01rem #ccc;border-bottom:solid 0.01rem #ccc;margin: 0.07rem 0;"
      >
        <span class="iconfont" style="font-size:0.24rem;padding:0 0.08rem;color:#c59e51;">&#xe604;</span>
      </h3>
    </a>
    <h3
      style="font-size:0.24rem;font-size:0.24rem;line-height:1rem;border-top:solid 0.01rem #ccc;border-bottom:solid 0.01rem #ccc;"
    >
      <span class="iconfont" style="font-size:0.24rem;padding:0 0.08rem;color:#c59e51;">&#xe622;</span>
    </h3>
    <a href="#/wo/yue" @click="bee()" style="color:#666;">
      <h3 style="font-size:0.24rem;line-height:1rem;border-top:solid 0.01rem #ccc;border-bottom:solid 0.01rem #ccc;">
        <span class="iconfont" style="font-size:0.24rem;padding:0 0.08rem;color:#c59e51;">&#xe626;</span>
      </h3>
    </a>
    <div class="kl" style="overflow:hidden;">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
export default {
  methods: {
    bee() {
      $(".kl").css({
        transform: "translateX(0)"
      });
    }
  }
};
</script>
<style>
.kl {
  position: absolute;
  left: 0;
  top: 0;
  background: #fff;
  min-height: 15rem;
  transform: translateX(100%);
  transition: 0.35s;
}
</style>


