<template>
  <mt-tabbar class="aa" v-model="selected">
    <mt-tab-item class="wenz" id="#/">
        <p class="iconfont" slot="icon">&#xe6d5;</p>
      
    </mt-tab-item>
    <mt-tab-item class="wenz" id="#/lie">
        <p class="iconfont" slot="icon">&#xe7f6;</p>
      
    </mt-tab-item>
    <mt-tab-item class="wenz" id="#/fa">
      <p slot="icon" class="iconfont">&#xe61a;</p>
      
    </mt-tab-item>
    <mt-tab-item class="wenz" id="#/wo">
        <p class="iconfont" slot="icon">&#xe603;</p>
        
    </mt-tab-item>
  </mt-tabbar>
</template>
<script>
import { Tabbar, TabItem } from 'mint-ui';
import { Indicator } from 'mint-ui';
export default {
    data () {
        return {
            selected:'#/'
        }
    },
    mounted () {
        // Indicator.open();

      Indicator.close();  
    },
    watch: {
        selected(a,b){
            location.href=a
        }
    },
    created () {
        this.selected='#'+this.$route.path
    }
}
</script>
<style>
    .aa{
        position: fixed;
    }
    .mint-tab-item-label{
        font-size: 0.28rem; 
        line-height: 0rem;
    }
    .mint-tab-item-icon .iconfont{
        font-size: 0.28rem;
    }
    .mint-tab-item-icon{
        padding-top: 0.06rem;
    }
</style>


